@import url(https://cdn.rawgit.com/hiun/NanumSquare/master/nanumsquare.css);
*{margin:0;padding:0;border:0;font-family:'Nanum Square',Malgun Gothic,"맑은 고딕",Roboto, Droid Sans, Helvetica, AppleSDGothicNeo, 'Apple SD 애플고딕 Neo', Apple-Gothic, '애플고딕', 'Tahoma', 'dotum', '돋움', 'gulim', '굴림', sans-serif;}
ul,ol,li{list-style:none;}
a{text-decoration:none;color:inherit}
img{border:0;vertical-align:middle}
.blind{visibility:hidden;overflow:hidden;position:absolute;top:0;left:0;width:0;height:0;font-size:0;line-height:0}	
::selection { color:inherit; background: transparent; }
::-moz-selection {color:inherit; background: transparent;} 

body{font-size:16px}
.sr-only{position: absolute;width: 1px;height: 1px;padding: 0;margin: -1px;overflow: hidden;clip: rect(0, 0, 0, 0);border: 0;}